/*上传选择器样式*/

#wrapper {
  margin: 0 auto;
  width: auto;
}

#container {
  border: 1px solid #dadada;
  color: #838383;
  font-size: 12px;
  background-color: #fff;
  padding: 1.2em 0;
}

#uploader .placeholder {
  border: 3px dashed #e6e6e6;
  min-height: 238px;
  padding-top: 158px;
  text-align: center;
  background: url(/static/img/image.png) center 93px no-repeat;
  color: #cccccc;
  font-size: 18px;
  position: relative;
}

.statusBar {
  border-top: 1px solid #dadada;
  margin-top: 20px;
  padding: 0 20px;
  line-height: 63px;
  vertical-align: middle;
  position: relative;
}

#chooseFile,
#startImgUpload {
  padding: 8px;
  background: linear-gradient(45deg, #0aacec, #0aeee3);
}

#uploaderPick,
.start-uploader,
.add-btn {
  font-size: 15px;
  line-height: 30px;
  padding: 0 20px;
  color: #fff;
  display: inline-block;
  margin-top: 20px;
  cursor: pointer;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}

#uploaderPick {
  font-size: 18px;
  line-height: 40px;
}

#uploaderPick a,
.start-uploader a,
.add-btn a {
  color: #fff;
  text-decoration: none;
}

#uploader .filelist {
  list-style: none;
  margin: 0;
  padding: 0;
}

#uploader .filelist:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  overflow: hidden;
  clear: both;
}

#uploader .filelist li {
  width: 100px;
  height: 100px;
  background: url(/static/img/bg.png) no-repeat;
  text-align: center;
  margin: 0 8px 20px 0;
  position: relative;
  display: inline;
  overflow: hidden;
  font-size: 12px;
  float: left;
}

#uploader .filelist li:hover .file-panel {
  height: 30px;
}

#uploader .filelist li p.log {
  position: relative;
  top: -45px;
}

#uploader .filelist li p.title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  top: 5px;
  text-indent: 5px;
  text-align: left;
}

#uploader .filelist li p.progress {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 8px;
  overflow: hidden;
  z-index: 50;
}

#uploader .filelist li p.progress span {
  display: none;
  overflow: hidden;
  width: 0;
  height: 100%;
  background: #1483d8 url(/static/img/progress.png) repeat-x;

  -webit-transition: width 200ms linear;
  -moz-transition: width 200ms linear;
  -o-transition: width 200ms linear;
  -ms-transition: width 200ms linear;
  transition: width 200ms linear;

  -webkit-animation: progressmove 2s linear infinite;
  -moz-animation: progressmove 2s linear infinite;
  -o-animation: progressmove 2s linear infinite;
  -ms-animation: progressmove 2s linear infinite;
  animation: progressmove 2s linear infinite;
  -webkit-transform: translateZ(0);
}

#uploader .filelist li .success {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 40px;
  width: 100%;
  z-index: 2;
  background: url(/static/img/success.png) no-repeat right bottom;
}

#uploader .filelist li .fail {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 40px;
  width: 100%;
  z-index: 2;
  background: url(/static/img/fail.png) no-repeat right bottom;
}

@-webkit-keyframes progressmove {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 17px 0;
  }
}

@-moz-keyframes progressmove {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 17px 0;
  }
}

@keyframes progressmove {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 17px 0;
  }
}

#uploader .filelist li p.imgWrap {
  position: relative;
  z-index: 2;
  line-height: 100px;
  vertical-align: middle;
  overflow: hidden;
  width: 100px;
  height: 100px;

  -webkit-transform-origin: 50% 50%;
  -moz-transform-origin: 50% 50%;
  -o-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;

  -webit-transition: 200ms ease-out;
  -moz-transition: 200ms ease-out;
  -o-transition: 200ms ease-out;
  -ms-transition: 200ms ease-out;
  transition: 200ms ease-out;
}

#uploader .filelist li img {
  width: 100%;
}

#uploader .filelist li p.error {
  background: #f43838;
  color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 28px;
  line-height: 28px;
  width: 100%;
  z-index: 100;
}

#uploader .filelist li .success {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 40px;
  width: 100%;
  z-index: 2;
  background: url(/static/img/success.png) no-repeat right bottom;
}

#uploader .filelist div.file-panel {
  position: absolute;
  height: 30px;
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, startColorstr='#80000000', endColorstr='#80000000')\0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 3;
}

#uploader .filelist div.file-panel span {
  width: 24px;
  height: 24px;
  display: inline;
  float: right;
  display: inline;
  text-indent: -9999px;
  overflow: hidden;
  background: url(/static/img/icons.png) no-repeat;
  margin: 5px 1px 1px;
  cursor: pointer;
}

#uploader .filelist div.file-panel span.rotateLeft {
  background-position: 0 -24px;
}

#uploader .filelist div.file-panel span.rotateLeft:hover {
  background-position: 0 0;
}

#uploader .filelist div.file-panel span.rotateRight {
  background-position: -24px -24px;
}

#uploader .filelist div.file-panel span.rotateRight:hover {
  background-position: -24px 0;
}

#uploader .filelist div.file-panel span.cancel {
  background-position: -48px -24px;
}

#uploader .filelist div.file-panel span.cancel:hover {
  background-position: -48px 0;
}

.total-progress {
  height: 16px;
  width: 80%;
  margin: 20px;
  display: inline-block;
  overflow: hidden;
  background-color: #f5f5f5;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
}

.total-progress-bar {
  float: left;
  width: 0;
  height: 100%;
  font-size: 12px;
  line-height: 16px;
  color: #fff;
  text-align: center;
  background: linear-gradient(45deg, #0aacec, #0aeee3);
  -webkit-box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
  -webkit-transition: width 0.6s ease;
  -o-transition: width 0.6s ease;
  transition: width 0.6s ease;
}
